@import url(https://fonts.googleapis.com/css2?family=Lato:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #52575d;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  padding: 0 30px;
  background-color: #f4f6ff;
  background-color: #52575d;

}

.gallery{

  padding: 20px;
  /*background-color:#f7f7f7;*/
  background-color: #52575d;
}

.cards{
  display: flex;
  align-items: center;
  flex-direction: column;
}

.card_box{
  display: flex;
  justify-content: center;
}

.card{
  display: flex;
  border-radius: 10px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 20px 20px;
  background-color: #febf63;
  margin : 30px 0;
  width: 80%;
  box-shadow: 0 15px 40px rgba(0,0,0,0.2);
}

.card_image{
  width: 100%;
  height: 20%;
  align-self: center;
  border: 8px solid #3c2c3e;
}


.card_heading{
  font-size: 26;
  font-family: 'Oswald', sans-serif;
  letter-spacing: 5px;
  text-transform: uppercase;
  color: #fff;
}
.card_detail{
  margin: 0 5px;
  flex:50% 1;
  font-family: 'Lato', sans-serif;
  font-size: 24px;
  padding: 0 60px;
  margin-bottom:10px;

}

.header{
  text-align: center;
  background-color:#e8505b;
  width: 80%;
  margin: auto;
  border-radius:20px;
}
.heading{
  text-transform: uppercase;
  font-size: 50px;
  letter-spacing: 10px;
  color: #FFF;
  
}

@media (max-width:900px){
  .heading{
    font-size: 30px;
    letter-spacing: 7px;
  }
  .card_heading{
      font-size: 18px;
  }
  .card_detail{
    font-size:14px;
    letter-spacing: 2px;
    padding: 0 40px;
  }
  .gallery{
    padding: 10px;
  }
  .App{
    padding: 0;
  }
}

.card_image2{
  width: 50%;
}

.card_box2{
  align-items: center;
  display: flex;
}

.part{
  flex: 50% 1;
}





@media (max-width:600px){
  .card_detail{
    font-size: 8px;
    letter-spacing: normal;
    padding: 0 20px;
  }
  .card_image{
    width: 100%;
    border: 4px solid #3c2c3e;
  }
  .card_box{
    flex-direction: column;
  }

}


